.contact-section {
  padding: 100px 0px 0px 0px;
  @media screen and (max-width: $mobile) {
    padding: 50px 0px 0px 0px;
  }
}
.contact-title {
  margin: 0px;
  text-align: center;
  color: $color_black;
  position: relative;
}

.contact-title:after {
  content: '';
  display: inline-block;
  background-color: $color_accent;
  width: 60px;
  height: 5px;
  border-radius: 5px;
  position: absolute;
  left: 50%;
  bottom: -18px;
  transform: translateX(-50%);
}



.contact-paragraf {
  display: block;
  width: 600px;
  text-align: center;
  margin: 30px auto 0px auto;
  font-family: $font_accent;
  font-weight: bold;
  font-size: 18px;
  line-height: 1.56;
  color: $color_black;

  @media screen and (max-width: $mobile) {
    width: 375px;
  }
}
.contact-form__input {
  margin: 40px 0px 0px 0px;
  padding-left: 10px;
  width: 420px;
  height: 60px;
  background: #e5e5e5;
  border-radius: 5px;
  border: none;

  &:not(:last-child) {
    margin-right: 10px;
  }
  @media screen and (max-width: $mobile) {
    width: 375px;
    margin: 10px 0px 0px 0px;
  }
}

.contact-form__input--message {
  margin: 10px 0px 0px 0px;
  padding-left: 10px;
  width: 1190px;
  height: 120px;
  background: #e5e5e5;
  border-radius: 5px;
  border: none;

  @media screen and (max-width: $mobile) {
    width: 375px;
    height: 100px;
  }
}

.contact-form__wrapping {
  @media screen and (min-width: $desktop) {
    display: flex;
    justify-content: center;
  }
}

.contact-button {
  padding: 0px;
  margin: 5px 0px 0px 0px;
  border-radius: 5px;
  min-width: 564px;
  height: 60px;

  text-align: center;
  border: none;

  color: $color_white;
  background-color: $color_accent;
  font-family: $font_accent;
  font-weight: bold;
  font-size: 12px;
  line-height: 1.67;

  &:hover {
    transform: scale(1.05);
  }

  @media screen and (max-width: $mobile) {
    min-width: 375px;
  }
}
.contact-footer {
  @media screen and (min-width: $desktop) {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }
}

.contact-adress {
  @media screen and (min-width: $desktop) {
    display: flex;
  }
}

.contact-adress__wrapping {
  display: flex;
  align-items: center;
  margin-right: 60px;
  @media screen and (max-width: $mobile) {
    margin: 10px 113px 10px 113px;
  }
}
.contact-adress__icon {
  margin-right: 5px;
}

.contact-adress__text {
  margin: 0px;

  font-family: $font_accent;
  font-size: 12px;
  line-height: 1;
  text-align: center;
  color: $color_black;
}

.contact-orange-line {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
}

.contact-message {
  margin-top: 20px;

  text-align: right;
}

.contact-map {
  @media screen and (min-width: $desktop) {
    width: 1190px;
    height: 600px;
  }
  @media screen and (max-width: $mobile) {
    width: 375px;
    height: 400px;
  }
}